<template>
  <img alt="Vue logo" src="./assets/img.jpg" width="150">
  <h3> Age : {{ age }} </h3>
  <button v-on:click ="showData"> Click for watch name </button>
  <button @click="showDataa"> Click for watch surname </button>
  <button @click="pluss(5)"> more age  </button> <!-- พิมพ์ .right หรือคำสั่งต่างๆเพื่อให้ทำงานโดยการปุ่มที่กำหนด -->
  <button @click="minuss(10)"> less age </button>
  <form> 
    <label for=""> ป้อนชื่อเล่น : </label>
    <input type="text" v-on:input="setnickname"> <!-- ใช้ v-on:input หรือ @input ก็ได้ -->
    <button type="submit"> กรุณากดยืนยัน </button>
  </form>

  <!-- งาน -->

  <form>
    <h1> งาน </h1>
    <label for=""> ป้อนชื่อจริง : </label>
    <input type="text" @input="setname"><br><br>

    <label for=""> ป้อนนามสกุล : </label>
    <input type="text" @input="setlastname"><br><br>
    
    <label for=""> ป้อนชื่อเล่น : </label>
    <input type="text" @input="setnickname"><br><br>

    <label for=""> ป้อนอายุ : </label>
    <input type="text" @input="setage"><br><br>

    <label for=""> ป้อนวัน / เดือน / ปีเกิด : </label>
    <input type="text" @input="setdate"><br><br>

    <label for=""> ป้อนสาขา : </label>
    <input type="text" @input="setmajor"><br><br>

    <label for=""> ป้อนห้องเรียน : </label>
    <input type="text" @input="setclass"><br><br>

    <label for=""> ป้อนคติประจำใจ : </label>
    <input type="text" @input="setmotto"><br><br>

      <label for=""> ป้อนสถานที่เรียนต่อ : </label>
    <input type="text" @input="setnext"><br><br>

    <label for=""> ป้อนวิชาที่ไม่ชอบ : </label>
    <input type="text" @input="setsub"><br><br>

    <button type="submit"> กรุณากดยืนยัน </button>

  </form>
    <h2> ชื่อจริง : {{ firstname }} </h2>
    <h2> นามสกุล : {{ lastname }} </h2>
    <h2> ชื่อเล่น : {{ nickname }} </h2>
    <h2> อายุ : {{ agee }} </h2>
    <h2> วันเดือนปีเกิด : {{ date }} </h2>
    <h2> สาขา : {{ major }} </h2>
    <h2> ห้องเรียน : {{ classIT }} </h2>
    <h2> คติประจำใจ : {{ motto }} </h2>
    <h2> สถานที่เรียนต่อ : {{ nextsch }} </h2>
    <h2> วิชาที่ไม่ชอบ : {{ dtlsub }} </h2>

</template>

<script>

export default {
  name: 'App',
  data(){
    return{
      firstname : "",
      lastname : "", 
      age : 20 ,
      agee : "",
      nickname : "",
      date : "",
      major : "",
      classIT : "",
      motto : "",
      nextsch : "",
      dtlsub : ""
    }
  },
  methods:{
    showData(){
      alert(this.firstname)
    },
    showDataa(){
      alert(this.lastname)
    },
    plus(){
      this.age++
    },
    pluss(val){
      this.age+=val
    },
    minus(){
      this.age--
    },
    minuss(val){
      this.age-=val
    },
    setname(event){
      // console.log(event.target.value); -> ตรวจสอบใน Console
      this.firstname = event.target.value
    },
    setlastname(event){
      this.lastname = event.target.value
    },
    setnickname(event){
      this.nickname = event.target.value
    },
    setage(event){
      this.agee = event.target.value
    },
    setdate(event){
      this.date = event.target.value
    },
    setmajor(event){
      this.major = event.target.value
    },
    setclass(event){
      this.classIT = event.target.value
    },
    setmotto(event){
      this.motto = event.target.value
    },
    setnext(event){
      this.nextsch = event.target.value
    },
    setsub(event){
      this.dtlsub = event.target.value
    },
  }
};


</script>

